import React, { useState, useEffect } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from '@material-ui/core';
import { createTableData } from '../helpers';
import { useUserDataValue } from '../context/UserDataContext';

export const ProfileTableData = () => {
  const [biometricState, setBiometricState] = useState([]);
  const { userData } = useUserDataValue();

  console.log('userData', userData);

  const getBiometricName = (key) => {
    switch (key) {
      case 'age':
        return 'Věk';
      case 'height':
        return 'Výška';
      case 'weight':
        return 'Váha';
      case 'gender':
        return 'Pohlaví';
      case 'palRatio':
        return 'PAL';
      default:
        return '';
    }
  };

  const getBiometricValue = (key, value) => {
    switch (key) {
      case 'gender':
        return value === 'male' ? 'Muž' : 'Žena';
      default:
        return value;
    }
  };

  useEffect(() => {
    const biometricTableData = [];

    for (const key in userData) {
      if (key !== 'userId' && key !== 'username' && key !== 'docId') {
        const dataValue = userData[key];

        biometricTableData.push(
          createTableData(
            getBiometricName(key),
            getBiometricValue(
              key,
              key === 'palRatio' ? dataValue.value : dataValue
            )
          )
        );
      }
    }

    setBiometricState(biometricTableData);
  }, [userData]);

  return (
    <TableContainer className='table' component={Paper}>
      <Table aria-label='customized table'>
        <TableHead className='table__head'>
          <TableRow>
            <TableCell>Moje Biometrika</TableCell>
            <TableCell></TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableBody className='table__body'>
          {biometricState.map((bio) => (
            <TableRow key={bio.title}>
              <TableCell>{bio.title}</TableCell>
              <TableCell style={{ textAlign: 'left', paddingRight: 0 }}>
                {bio.value}
              </TableCell>
              <TableCell style={{ textAlign: 'center', paddingLeft: 0 }}>
                {bio.title === 'Výška'
                  ? 'cm'
                  : bio.title === 'Váha'
                  ? 'kg'
                  : ''}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
