import { useFoodStateValue } from '../context/FoodStateContext';
import { calculateFoodGrammage } from '../helpers/calculateFoodGrammage';

export const useFoodStateHelpers = () => {
  const { foodState } = useFoodStateValue();

  const isFoodStateEmpty = foodState.length === 0;

  const isFoodAvailable = (title) => {
    return foodState.some((food) => food.title === title);
  };

  const getAvailableFood = (title) => {
    return foodState.find((food) => food.title === title);
  };

  const getAvailableFoodIndex = (title) => {
    return foodState.findIndex((food) => food.title === title);
  };

  const calculateFoodValue = (foodList, nutrientName, nutrientType) => {
    let totalValue = 0;
    let currentValue = 0;

    // console.log(foodList, nutrientName, nutrientType);

    if (foodList.length > 0) {
      foodList.forEach(({ grammage, nutrition, grammageInfo }) => {
        const grammageMultiplier =
          calculateFoodGrammage(grammage.value, grammage.unit, grammageInfo) /
          100;

        let nutrientValue;

        // console.log(nutrientName, nutrientValue, nutrientType);
        nutrientValue = nutrition[nutrientType][nutrientName];

        currentValue = nutrientValue * grammageMultiplier;

        totalValue = totalValue + currentValue;
      });
    }

    return totalValue % 1 !== 0
      ? Number(totalValue.toFixed(1))
      : Number(totalValue);
  };

  return {
    isFoodStateEmpty,
    isFoodAvailable,
    getAvailableFood,
    getAvailableFoodIndex,
    calculateFoodValue,
  };
};
