import React from 'react';
import { useNutrientStatusValue } from '../hooks/useNutrientStatusValue';
import { DataSectionValue } from '../compontents/DataSectionValue';

export const MacroDataSection = ({
  title,
  name,
  info,
  isMacro,
  subSection,
  calculateValue,
  functionState,
  caloricValue,
  isMenu,
}) => {
  const {
    renderComplexNutrientStatus,
    renderSimpleNutrientStatus,
    renderPercentageNutrientStatus,
  } = useNutrientStatusValue();

  const renderMacroStatus = () => {
    if (name === 'protein')
      return renderComplexNutrientStatus(
        0.8,
        2,
        //! Append name to total
        calculateValue(functionState, 'total', name)
      );
    if (name === 'carbohydrate')
      return renderComplexNutrientStatus(
        2,
        3.8,
        calculateValue(functionState, 'total', name)
      );
    if (name === 'fat')
      return renderComplexNutrientStatus(
        0.8,
        1.2,
        calculateValue(functionState, 'total', name)
      );
  };

  return (
    <div className='data-section'>
      <h3 className='data-section__title'>{title}</h3>
      <div className='data-section__content'>
        <DataSectionValue
          name={isMacro ? 'total' : name}
          title='Celkem'
          nutrition={title}
          info={info}
          calculateValue={calculateValue}
          functionState={functionState}
          nutritionType={name}
          renderStatus={renderMacroStatus}
          isMenu={isMenu}
          unit='g'
        />
        {/* {subSection.forEach((yo) => console.log(yo))} */}
        {subSection.subNutrients &&
          subSection.subNutrients.map(({ title, name, info }) => {
            const renderSubMacroStatus = () => {
              switch (name) {
                case 'dietaryFiber':
                  return renderSimpleNutrientStatus(
                    21,
                    70,
                    30,
                    81,
                    calculateValue(functionState, name, 'carbohydrate')
                  );
                case 'sugars':
                  return renderSimpleNutrientStatus(
                    0,
                    24,
                    0,
                    36,
                    calculateValue(functionState, name, 'carbohydrate')
                  );
                //! invesigate
                case 'starch':
                  return renderSimpleNutrientStatus(
                    0,
                    0,
                    0,
                    0,
                    calculateValue(functionState, name, 'carbohydrate')
                  );
                case 'saturatedFat':
                  return renderPercentageNutrientStatus(
                    7,
                    caloricValue,
                    calculateValue(functionState, name, 'fat')
                  );
                case 'monounsaturatedFat':
                  return renderPercentageNutrientStatus(
                    20,
                    caloricValue,
                    calculateValue(functionState, name, 'fat'),
                    true
                  );
                case 'polyunsaturatedFat':
                  return renderPercentageNutrientStatus(
                    10,
                    caloricValue,
                    calculateValue(functionState, name, 'fat'),
                    true
                  );
                case 'transFattyAcids':
                  return renderPercentageNutrientStatus(
                    1,
                    caloricValue,
                    calculateValue(functionState, name, 'fat')
                  );
                default:
                  return renderSimpleNutrientStatus(
                    0,
                    100,
                    0,
                    100,
                    calculateValue(functionState, name, 'macro')
                  );
              }
            };

            return (
              <DataSectionValue
                key={title}
                name={name}
                nutrition={title}
                info={info}
                title={title}
                calculateValue={calculateValue}
                functionState={functionState}
                renderStatus={renderSubMacroStatus}
                nutritionType={subSection.section}
                isMenu={isMenu}
                unit='g'
              />
            );
          })}
      </div>
    </div>
  );
};
