import React from 'react';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import { makeStyles } from '@material-ui/core/styles';
import { MenuItemsListModal } from './MenuItemsListModal';
import { NutritionInfo } from './info/NutritionInfo';

const useStyles = makeStyles((theme) => ({
  modal: {
    width: '90%',
    height: '70%',
    overflow: 'scroll',
  },
}));

export const DataSectionValue = ({
  name,
  title,
  info,
  nutrition,
  calculateValue,
  functionState,
  nutritionType,
  renderStatus,
  isMenu,
  unit,
}) => {
  const isMenuNutrition = renderStatus && isMenu;

  const classes = useStyles();

  const minMaxValue =
    renderStatus &&
    `${Math.round(renderStatus().minValue)} / ${Math.round(
      renderStatus().maxValue
    )}`;

  const renderState = () =>
    isMenuNutrition && renderStatus().status.includes('success')
      ? 'success'
      : 'error';

  console.log(nutritionType, name);

  return (
    <MenuItemsListModal modalStyle={classes.modal}>
      <div
        className={`data-value ${isMenu && `data-value--${renderState()}`} `}
      >
        <p className='data-value__title'>{title}:</p>
        {renderStatus && <p className='data-value__spread'>{minMaxValue}:</p>}
        <p
          className={`data-value__value ${
            isMenuNutrition && renderStatus().status
          }`}
        >
          {calculateValue(functionState, name, nutritionType)} {unit}
          <ArrowDownwardIcon className='data-value__down' />
          <ArrowUpwardIcon className='data-value__up' />
        </p>
      </div>
      <NutritionInfo info={info} nutrition={nutrition} />
    </MenuItemsListModal>
  );
};
