import React, { useEffect } from 'react';
import { Container } from '@material-ui/core';
import { Donut } from '../compontents/Donut';
import { useFoodStateHelpers } from '../hooks/useFoodStateHelpers';
import { useUserDataValue } from '../context/UserDataContext';
import { MacroDataSection, MicroDataSection } from '../compontents';
import { useFoodStateValue } from '../context/FoodStateContext';

import {
  MACRO_SECTION_DATA,
  VITAMINS_SECTION_DATA,
  MINERALS_SECTION_DATA,
} from '../constants/nutrients';

import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';

export const RecipeNutrition = () => {
  const { calculateFoodValue } = useFoodStateHelpers();
  const { foodState } = useFoodStateValue();
  const { getBMRValue, getTEEValue } = useUserDataValue();

  const getDonutData = (nutritionType, mode = 'macro') => {
    console.log('mode', mode);
    if (mode === 'macro')
      return nutritionType.map(({ name }) =>
        calculateFoodValue(foodState, 'total', name) == 0
          ? 0.1
          : calculateFoodValue(foodState, 'total', name)
      );

    //! convert to same unit
    if (mode === 'vitamins')
      return nutritionType.map(({ name }) =>
        calculateFoodValue(foodState, name, 'vitamins') == 0
          ? 0.1
          : calculateFoodValue(foodState, name, 'vitamins')
      );

    if (mode === 'minerals')
      return nutritionType.map(({ name }) =>
        calculateFoodValue(foodState, name, 'minerals') == 0
          ? 0.1
          : calculateFoodValue(foodState, name, 'minerals')
      );
  };

  const minMaxValue = `${getBMRValue()} / ${getTEEValue()}`;

  const caloricValue = calculateFoodValue(foodState, 'total', 'calories');

  const getRecipePrice = (foodState) => {
    const recipeEstimatedPrice = foodState.reduce(
      (totalValue, { grammage, price }) => {
        const convertedPrice =
          grammage.valueInMainUnit *
          ((price.lowestPricePerMainUnit + price.highestPricePerMainUnit) / 2);
        return Math.round(totalValue + convertedPrice) || 0;
      },
      0
    );

    const recipeLowestPrice = foodState.reduce(
      (totalValue, { grammage, price }) => {
        const convertedPrice =
          grammage.valueInMainUnit * price.lowestPricePerMainUnit;
        // console.log(grammage.valueInMainUnit);
        // console.log(
        //   Number(grammage.valueInMainUnit) * price.lowestPricePerMainUnit
        // );

        return Math.round(totalValue + convertedPrice) || 0;
      },
      0
    );

    const recipeHighestPrice = foodState.reduce(
      (totalValue, { grammage, price }) => {
        const convertedPrice =
          grammage.valueInMainUnit * price.highestPricePerMainUnit;
        return Math.round(totalValue + convertedPrice) || 0;
      },
      0
    );

    return { recipeEstimatedPrice, recipeLowestPrice, recipeHighestPrice };
  };

  // useEffect(() => {
  //   // console.log(userData);
  // }, [userData]);

  return (
    <Container className='recipe-nutrition'>
      <Donut getDonutData={getDonutData} />
      <div className='recipe-nutrition__data'>
        <div className='data-section'>
          <h3 className='data-section__title '>Kalorie</h3>
          <div className='data-section__row'>
            <div className='data-value'>
              <p className='data-value__title'>Celkem:</p>
              <p className='data-value__spread'>{minMaxValue}</p>
              <p className='data-value__value'>
                {calculateFoodValue(foodState, 'total', 'calories')} kcal
                <ArrowDownwardIcon className='data-value__down' />
                <ArrowUpwardIcon className='data-value__up' />
              </p>
            </div>
          </div>
        </div>
        {MACRO_SECTION_DATA.map(({ title, name, subSection, isMacro }) => (
          <MacroDataSection
            key={title}
            title={title}
            name={name}
            isMacro={isMacro}
            subSection={subSection}
            calculateValue={calculateFoodValue}
            functionState={foodState}
            caloricValue={caloricValue}
          />
        ))}

        <MicroDataSection
          sectionType={VITAMINS_SECTION_DATA}
          nutrientTitle='Vitamíny'
          nutrientType='vitamins'
          calculateValue={calculateFoodValue}
          functionState={foodState}
        />

        <MicroDataSection
          sectionType={MINERALS_SECTION_DATA}
          nutrientTitle='Minerály'
          nutrientType='minerals'
          calculateValue={calculateFoodValue}
          functionState={foodState}
        />
        <div className='recipe-nutrition__micro'>
          <div className='data-section'>
            <h3 className='data-section__title'>Cena</h3>
            <div className='data-section__content'>
              <div className='data-value' style={{ flexBasis: 'auto' }}>
                <p className='data-value__title'>Předpokládaná Cena:</p>

                <p className={`data-value__value`}>
                  {getRecipePrice(foodState).recipeLowestPrice} -{' '}
                  {getRecipePrice(foodState).recipeHighestPrice} CZK
                </p>

                <p className='data-value__title'>Medián:</p>

                <p className={`data-value__value`}>
                  {getRecipePrice(foodState).recipeEstimatedPrice} CZK
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Container>
  );
};
