import { calculateFoodGrammage } from '../helpers/calculateFoodGrammage';

export const useMenuItemsList = () => {
  const calculateMenuItemsValue = (menuList, nutrientName, nutrientType) => {
    let totalValue = 0;
    let currentValue = 0;

    if (menuList.length > 0) {
      menuList.map(({ itemsList }) => {
        itemsList.forEach(({ grammage, nutrition, grammageInfo }) => {
          const grammageMultiplier =
            calculateFoodGrammage(grammage.value, grammage.unit, grammageInfo) /
            100;

          let nutrientValue;

          // console.log(nutrientName, nutrientValue, nutrientType);
          nutrientValue = nutrition[nutrientType][nutrientName];

          currentValue = nutrientValue * grammageMultiplier;
          totalValue = totalValue + currentValue;
        });
      });
    }

    return totalValue % 1 !== 0
      ? Number(totalValue.toFixed(1))
      : Number(totalValue);
  };

  return {
    calculateMenuItemsValue,
  };
};
